.glass {
  position: absolute;
  text-align: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1;
  background-image: url(data:image/png;base64,);
  backdrop-filter: blur(20px);
}

@media (prefers-color-scheme: dark) {
  .glass {
    background-color: rgba(0, 0, 0, 0.2);
  }
}

@media (prefers-color-scheme: light) {
  .glass {
    background-color: rgba(255, 255, 255, 0.2);
  }
}