.name {
  display: flex;
  justify-content: center;
  align-items: center;
}

.letter {
  position: relative;
  margin: 0 min(10px, calc(90vw / 90));
  height: min(200px, calc(90vw / 4.5));
  width: auto;
  transition: all 4s ease-in-out;
  animation: var(--animation) 6s ease-in-out infinite;
}

.letter img {
  height: 100%;
  width: auto;
}

@keyframes one {
  0% , 100% {
    transform: translate(var(--displacedX), var(--displacedY));
  }
  35% {
    transform: translate(calc(var(--displacedX) + 3px), calc(var(--displacedY) + 3px));
  }
  75% {
    transform: translate(calc(var(--displacedX) - 3px), calc(var(--displacedY) - 3px));
  }
}

@keyframes two {
  0% , 100% {
    transform: translate(var(--displacedX), var(--displacedY));
  }
  35% {
    transform: translate(calc(var(--displacedX) + 3px), calc(var(--displacedY) - 3px));
  }
  75% {
    transform: translate(calc(var(--displacedX) - 3px), calc(var(--displacedY) + 3px));
  }
}

@keyframes three {
  0% , 100% {
    transform: translate(var(--displacedX), var(--displacedY));
  }
  35% {
    transform: translate(calc(var(--displacedX) - 3px), calc(var(--displacedY) + 3px));
  }
  75% {
    transform: translate(calc(var(--displacedX) + 3px), calc(var(--displacedY) - 3px));
  }
}

@media (pointer: none) {
  @keyframes one {
    0% , 100% {
      transform: translate(0, 0);
    }
    35% {
      transform: translate(3px, 3px);
    }
    75% {
      transform: translate(-3px, 3px);
    }
  }
  
  @keyframes two {
    0% , 100% {
      transform: translate(0, 0);
    }
    35% {
      transform: translate(3px, -3px);
    }
    75% {
      transform: translate(-3px, 3px);
    }
  }
  
  @keyframes three {
    0% , 100% {
      transform: translate(0, 0);
    }
    35% {
      transform: translate(-3px, 3px);
    }
    75% {
      transform: translate(3px, -3px);
    }
  }
}